"use client";

import { useState, useEffect } from 'react';
import styles from '../../components/catalogo/catalogoPage.module.css';
import CardCatalogo from '@/components/catalogo/cardCatalogo';
import Breadcrumb from '@/components/ui/Breadcrums/Breadcrums';
import { api } from '@/services/api';
import { usePathname } from 'next/navigation';

const ModelosPage = () => {
  const pathname = usePathname();
  
  // Extraer categorÃ­a y marca de la URL
  const pathParts = pathname.split('/').filter(part => part);
  const nameCategory = pathParts[0] || '';




  // ðŸ”¹ Estado para guardar los modelos
  const [modelos, setModelos] = useState<{ id: string; imageSrc: string; text: string }[]>([]);
  const [loading, setLoading] = useState(true);

  // ðŸ”¹ FunciÃ³n para obtener los modelos desde la API
  const fetchModelos = async (nameCategory: string) => {
    try {
 
      const response = await api.categories.getAll(nameCategory)
   

      
    setModelos(response.data.map((modelo: any) => ({
        id: modelo.mfa_id,
        imageSrc: modelo.imageSrc || "/placeholder.jpg", // Default image if not provided
        text: modelo.mfa_pref || modelo.mfa_pref, // Default text if not provided
      })
    ))

    } catch (error) {
      console.error("Error obteniendo modelos:", error);
      setModelos([]); // ðŸ”¥ Si hay error, deja el estado vacÃ­o
    } finally {
      setLoading(false); // ðŸ”¥ Detener la carga
    }
  };

  // ðŸ”¹ Llamar API cuando `nameCategory` o `marcaId` cambien
  useEffect(() => {
    if (nameCategory) {
      setLoading(true);
      fetchModelos(nameCategory);
    }
  }, [nameCategory]);

  return (
    <div>
      {/* ðŸ“Œ Banner Principal */}
      {/* <Banner imageSrc="/banners/catalogo.png" altText="Banner de catÃ¡logo" mt="-10" /> */}

      {/* ðŸ“Œ Contenedor Principal */}
      <div className={styles.catalogContainer}>
        {/* ðŸ“Œ Contenedor de Breadcrumb alineado con las tarjetas */}
        <div className={styles.breadcrumbWrapper}>
          <Breadcrumb categoria={nameCategory} />
        </div>

        {/* ðŸ“Œ Contenedor de Tarjetas */}
        {loading ? (
          <p className="text-center text-gray-500">Cargando modelos...</p>
        ) : (
          <div className={styles.gridContainer}>
            {modelos.length > 0 ? (
              modelos.map((modelo) => (
                <CardCatalogo
                  key={modelo.id}
                  imageSrc={modelo.imageSrc} 
                  text={modelo.text}      // supongo que esto va a cambiar como el resto de propiedades pero ya desde esta perspectiva general serÃ¡ mÃ¡s facil 
                  id={modelo.id}
                  categoria={nameCategory}
                  marca=""
                  modelo=""
                  level="marca"
                />
              ))
            ) : (
              <p className="text-center text-gray-500">No hay modelos disponibles</p>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default ModelosPage;
